import stocks from './../../data/stocks';

const state = {
    stocks: []
}

const mutations = {
    'SET_STOCKS'(state, stocks) {
        state.stocks = stocks;
    },
    'RND_STOCKS'(state) {
        state.stocks.forEach( stock => {
            stock.px = Math.round( stock.px * (1 + Math.random() - 0.5));
        });
    }
}

const actions = {
    buyStock: ({ commit }, ord) => {
        commit('BUY_STOCK', ord);
    },
    initStocks: ({ commit }) => {
        commit('SET_STOCKS', stocks)
    },
    randomizeStocks: ({ commit }) => {
        commit('RND_STOCKS');
    }
}

const getters = {
    stocks: state => {
        return state.stocks;
    }
}

export default {
    state,
    mutations,
    actions,
    getters
}